body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Content{
    margin-top:72px;
}
.Toolbar{
    height: 50px;
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    background-color: whitesmoke;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 10px;
    box-sizing: border-box;
    z-index: 90;
}
/* .Toolbar nav{
    height: 100%;
} */
.Logo {
    padding-top: 0.6%;
    width: 20%;
    height: 97%;
}
.Nav{
 width: 80%;
 height: 50px;
}
.NavList{
    margin: 0;
    padding: 0;
    list-style: none;
    display: inline-block;
    flex-flow: column;
    align-items: center;
    height: 100%;
}
.NavRight{
    float: right;
    height: 50px;
}
.NavLeft{
    float: left;
    height: 50px;
}
@media (max-width: 499px){
    .Nav{
        display: none;
       }
    .Logo{
        padding-top: 2.5%;
        width: 75%;
        height: 97%;
    }   
}
.Logo{
    background-color: white;
    padding: 1px;
    height: 80%;
    float: right;
    box-sizing: border-box;
    border-radius: 5px;
}

.Logo img{
    height: 100%;
}
.NavigationItem{
    font-family: "Open Sans","Helvetica Neue",Helvetica,Arial,sans-serif;
    margin: 10px 0;
    box-sizing: border-box;
    display: block;
    max-width: 100%;
    font-size: 14px;
}

.NavigationItem a {
    color: black;
    text-decoration: none;
    width: 100%;
    box-sizing: border-box;
    display: block;
    margin-left: 20px;
    height: 20px;
}
.NavigationItem a:hover,
.NavigationItem a:active,
.NavigationItem a.active{
    color: #40A4C8;
}
@media (min-width: 500px){
    .NavigationItem{
        margin: 0;
        display: inline-block;
        height: 100%;
        width: auto;
        align-items: center;
    }
    .NavigationItem a{
        color: black;
        height: 100%;
        padding: 16px 10px;
        border-bottom: 4px solid transparent;
    }
    .NavigationItem a:hover,
    .NavigationItem a:active,
    .NavigationItem a.active{
        background-color: whitesmoke;
        border-bottom: 4px solid #40A4C8;
        color: black;
    }
    

}
.DrawerToggle{
    width:40px;
    height: 100%;
    display: flex;
    flex-flow: column;
    justify-content: space-around;
    align-items: center;
    padding: 10px 0;
    box-sizing: border-box;
    cursor: pointer;
}
.DrawerToggle div{
    width: 90%;
    height: 3px;
    background-color: #40A4C8;
}
@media (min-width:500px){
    .DrawerToggle{
        display: none
    }
}
.SideDrawer{
    position: fixed;
    width: 280px;
    max-width: 70%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 200;
    background-color: white;
    transition: -webkit-transform 0.3s ease-out;
    transition: transform 0.3s ease-out;
    transition: transform 0.3s ease-out, -webkit-transform 0.3s ease-out;
}

@media(min-width: 500px){
    .SideDrawer{
        display: none
    }
}

.Open{
    -webkit-transform: translateX(0);
            transform: translateX(0);
}
.Close{
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
}
.Logo{
    background-color: white;
    padding: 5px;
    height: 50px;
    float: left;
    box-sizing: border-box;
    border-radius: 5px;
}
.Nav{
    width: 80%;
    height: 50px;
   }
   .NavList{
       margin: 0;
       padding: 0;
       list-style: none;
       display: inline-block;
       flex-flow: column;
       align-items: center;
       height: 100%;
   }
   .NavRight{
       float: right;
       height: 50px;
   }
   .NavLeft{
       float: left;
       height: 50px;
   }
  
   @media (max-width: 499px){
    .NavLeft{
        float: left;
        height: 50px;
        width: 100%;
    }
     
   }
.Backdrop{
    width: 100%;
    height: 100%;
    position: fixed;
    z-index: 100;
    left: 0;
    top: 0;
    background-color: rgba(0, 0, 0, 0.5)
}
.DataCatalogs{
    width: 100%;
    text-align: center;
}



.DataCatalog{
    font-family: "Open Sans","Helvetica Neue",Helvetica,Arial,sans-serif;
    width: 37%;
    border: 1px solid #eee;
    box-shadow: 0 2px 3px #ccc;
    padding: 10px;
    display: inline-block;
    margin: 20px ;
    box-sizing: border-box;
    text-align: left;
}
.DataCatalog span{
    font-weight: bold
}
.DataCatalog a button{
    background-color: #40A4C8;
    padding: 10px 15px;
    color: white;
    border: 1px solid #40A4C8;
    border-radius: 5px;
    align-items: center;
}
.DataCatalog p{
    font-size: 14px;
    color: #666666;
    line-height: 1.35em;
    
}
.DataCatalog a button:hover{
    background-color: #39819b;
    padding: 10px 15px;
    color: white;
    border: 1px solid #39819b;
    border-radius: 5px;
}
.DataCatalog a{
    color: #39819b;
    text-decoration: none ;
}
.DataCatalog a:hover{
    color: #00bbff;
    text-decoration: none ;
}
@media(max-width: 499px){
    .DataCatalog{
        width: 90%;
        border: 1px solid #eee;
        box-shadow: 0 2px 3px #ccc;
        padding: 10px;
        display: inline-block;
        margin: 20px ;
        box-sizing: border-box;
        text-align: left;
        margin: 10px;
        min-height: 250px;
    }
}
/*This CSS Spinner  I borrowed from https://projects.lukehaas.me/css-loaders/*/
.Loader {
    color: #00a800;
    font-size: 90px;
    text-indent: -9999em;
    overflow: hidden;
    width: 1em;
    height: 1em;
    border-radius: 50%;
    margin: 72px auto;
    position: relative;
    -webkit-transform: translateZ(0);
    transform: translateZ(0);
    -webkit-animation: load6 1.7s infinite ease, round 1.7s infinite ease;
    animation: load6 1.7s infinite ease, round 1.7s infinite ease;
  }
  @-webkit-keyframes load6 {
    0% {
      box-shadow: 0 -0.83em 0 -0.4em, 0 -0.83em 0 -0.42em, 0 -0.83em 0 -0.44em, 0 -0.83em 0 -0.46em, 0 -0.83em 0 -0.477em;
    }
    5%,
    95% {
      box-shadow: 0 -0.83em 0 -0.4em, 0 -0.83em 0 -0.42em, 0 -0.83em 0 -0.44em, 0 -0.83em 0 -0.46em, 0 -0.83em 0 -0.477em;
    }
    10%,
    59% {
      box-shadow: 0 -0.83em 0 -0.4em, -0.087em -0.825em 0 -0.42em, -0.173em -0.812em 0 -0.44em, -0.256em -0.789em 0 -0.46em, -0.297em -0.775em 0 -0.477em;
    }
    20% {
      box-shadow: 0 -0.83em 0 -0.4em, -0.338em -0.758em 0 -0.42em, -0.555em -0.617em 0 -0.44em, -0.671em -0.488em 0 -0.46em, -0.749em -0.34em 0 -0.477em;
    }
    38% {
      box-shadow: 0 -0.83em 0 -0.4em, -0.377em -0.74em 0 -0.42em, -0.645em -0.522em 0 -0.44em, -0.775em -0.297em 0 -0.46em, -0.82em -0.09em 0 -0.477em;
    }
    100% {
      box-shadow: 0 -0.83em 0 -0.4em, 0 -0.83em 0 -0.42em, 0 -0.83em 0 -0.44em, 0 -0.83em 0 -0.46em, 0 -0.83em 0 -0.477em;
    }
  }
  @keyframes load6 {
    0% {
      box-shadow: 0 -0.83em 0 -0.4em, 0 -0.83em 0 -0.42em, 0 -0.83em 0 -0.44em, 0 -0.83em 0 -0.46em, 0 -0.83em 0 -0.477em;
    }
    5%,
    95% {
      box-shadow: 0 -0.83em 0 -0.4em, 0 -0.83em 0 -0.42em, 0 -0.83em 0 -0.44em, 0 -0.83em 0 -0.46em, 0 -0.83em 0 -0.477em;
    }
    10%,
    59% {
      box-shadow: 0 -0.83em 0 -0.4em, -0.087em -0.825em 0 -0.42em, -0.173em -0.812em 0 -0.44em, -0.256em -0.789em 0 -0.46em, -0.297em -0.775em 0 -0.477em;
    }
    20% {
      box-shadow: 0 -0.83em 0 -0.4em, -0.338em -0.758em 0 -0.42em, -0.555em -0.617em 0 -0.44em, -0.671em -0.488em 0 -0.46em, -0.749em -0.34em 0 -0.477em;
    }
    38% {
      box-shadow: 0 -0.83em 0 -0.4em, -0.377em -0.74em 0 -0.42em, -0.645em -0.522em 0 -0.44em, -0.775em -0.297em 0 -0.46em, -0.82em -0.09em 0 -0.477em;
    }
    100% {
      box-shadow: 0 -0.83em 0 -0.4em, 0 -0.83em 0 -0.42em, 0 -0.83em 0 -0.44em, 0 -0.83em 0 -0.46em, 0 -0.83em 0 -0.477em;
    }
  }
  @-webkit-keyframes round {
    0% {
      -webkit-transform: rotate(0deg);
      transform: rotate(0deg);
    }
    100% {
      -webkit-transform: rotate(360deg);
      transform: rotate(360deg);
    }
  }
  @keyframes round {
    0% {
      -webkit-transform: rotate(0deg);
      transform: rotate(0deg);
    }
    100% {
      -webkit-transform: rotate(360deg);
      transform: rotate(360deg);
    }
  }
